@include tablet {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr;
    }
}

.client-story{
    margin-bottom: 2rem;
    margin-left: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid gray;
    background-color: white;

    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type{
        margin-bottom:0;
    }

    
}

.texto-client-story{


    a {
        text-decoration: none;
    }

    h4{
        margin-left: 0px;
        margin-top: 2px;
        line-height: 1.2px;
        font-size:2rem;
    }

    hr{
        width: 18%;
        margin-left: 0%;

    }
    
    span{
        font-size: 1.3rem;
    }

}

.imagen-client-story {
    img{
        max-width: 100%;
        max-height:100%;
    }

}

.client-story:hover{
    transform: scale(1.1); 
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);

}

.signature{
    font-size: 1.2rem;
    color: gray;
}