.paginacion {
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    @include tablet {
        justify-content: flex-end;
    }

    &__enlace {
        color: #0cc;
        font-size: 1.6rem;

        &--texto {
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid #0cc;

            &:hover {
                background-color: blue($color: #000000);
                color: white;
            }
        }
        &--numero {
            font-weight: bold;
            display: none;
            @include tablet {
                display: inline;
            }
        }
        &--actual {
            color: gray;
            font-weight: bold;
            display: none;
            @include tablet {
                display: inline;
            }
        }
    }
}