.contenido-nosotros{
    @include tablet {
        @include grid(2, 2rem);
    }

    padding-bottom: 5rem;
}

.quien-somos-imagen{
    padding-left: 20rem;
    img{
        width: 300px;  
        height: auto;  
        padding-top: 2rem;
    }


}
.quien-somos-texto{
    margin: 0;
    h4{
        font-size:2.2rem;
        color: #00cccc;

    }
}

.que-hacemos-imagen{
    padding-left: 20rem;
    img{
        width: 350px;  
        height: auto;  
        padding-top: 4rem;

    }


}

.que-hacemos-texto{
    margin: 0;
    h4{
        font-size:2.2rem;
        color: #00cccc;
    }
}