@include tablet {
    .seccion-escritores {
        @include grid(3, 2rem);
    
    }
}


.escritor {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 5px; 
    
    img {
        height: 200px;
        width: 200px;
        border-radius: 50%; 
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: 5px;

    }

    h4 {
        display: block;
        font-weight: bold;
        font-size: 18px;
    }
    .precio-hora {
        color:#00cccc;
        font-weight: bold;
        text-align: right;
    }
    .especialidades {
        line-height: 2rem;
    }
    p{
        line-height: 1rem;

    }
}

.escritor:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

.escritor-descripcion {
    padding: 2px 16px;
}

.view-more {
    margin-top: 3rem;
    display: flex;
    justify-content: flex-end;

}

.filtros {
    background-color: white;
    margin-top: 2rem;
    border-radius: 1rem;
    padding: 2rem;
    .filtros-inputs {
        @include tablet {
            display: flex;
            align-items:center;
            gap: 3rem;
        }
    }
    h2 {
        color:black;
        font-weight: bold;
        font-size: 2rem;
        @include tablet {
            margin: 0;
        }
    }

    .campo {
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;
        @include tablet {
            margin-bottom: 0;
        }
    }

    input[type="radio"] {
        color:#00cccc;
        width: 1.6rem;
        height: 1.6rem;
    }

}