

.contact-background{
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/background-contact.jpg");
    background-position: center center;
    background-size: cover;
    padding: 2.5rem 0;
    opacity: 5.0;
    text-align: center;
    position: relative;
    color: white;
    height: 27rem;
    h2 {
        font-size: 2.5rem;
        font-weight: bold;
    }

    p {
        font-weight: bold;

    }

    hr {
        width: 25%;
        background-color: white;
      }
}

