.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2rem;
    @include tablet{
        flex-direction:row;
        justify-content: space-between;
    }
}
.navegacion{

    display: none;

    @include tablet {
        display:flex;
        align-items: center;
    }
    a {
        color: white;
        text-decoration: none;
        font-size: 20px;
        display: block;
        text-align: center;
        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;
        

            &:last-of-type {
                margin-right: 0;
            }
        }
    }

    a:hover {
        text-decoration: underline;
    }

}

.mobile-menu{
    @include tablet {
        display: none;
    }

    img{
        width: 3rem;
    }
}

.mostrar{
    display: flex;
    flex-direction: column;

    @include tablet{
        flex-direction: row;
    }
}
