
.contenedor-boton {
  @include tablet {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 5rem;
  }
}
.boton {
  color:#000000;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.4rem;
  @include tablet {
      width: auto;
  }
}


.table {
  width: 100%;
  border-collapse: collapse;
  border-radius: 10px;

  &__thead {
      background-color: #0cc;;
      color: white;
  }
  &__th {
      padding: 1rem;
      text-align: left;
  }

  &__tr {
      &:nth-of-type(even) {
          background-color: gray;
      }
  }

  &__td {
      padding: 1.5rem 1rem;

      &--acciones {
          @extend .table__td;
          display: flex;
          justify-content: flex-end;
          gap: 2rem;
          align-items: center;
      }
  }

  &__accion {
      all: unset;
      text-transform: uppercase;
      font-size: 1.4rem;
      font-weight: bold;
      &--editar {
          color: blue($color: #000000);
      }
      &--eliminar {
         color: red($color: #000000);
      }
      &:hover {
          cursor: pointer;
      }
  }
}

.alerta{
    padding: 1.25rem;
    color: white;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    font-weight: bold;
    text-transform: uppercase;
    border-radius: 5px;


    &.error{
        background-color: red;
    }

    &.exito{
        background-color: green;
    }

    .closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
  }
  
  .closebtn:hover {
    color: black;
  }
}

.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
  align-items: center;
    padding: 1.5rem 1rem;

}

/* Style the tab */
.tab {
    overflow: hidden;
    border: 1px solid #ccc;
    background-color: #f1f1f1;
  }
  
  /* Style the buttons that are used to open the tab content */
  .tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
  }
  
  /* Change background color of buttons on hover */
  .tab button:hover {
    background-color: #ddd;
  }
  
  /* Create an active/current tablink class */
  .tab button.active {
    background-color: #ccc;
  }
  
  /* Style the tab content */
  .tabcontent {
    display: none;
    padding: 6px 12px;
    border: 1px solid #ccc;
    border-top: none;
  }