}

.alerta {
  padding: 1.25rem;
  color: white;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 5px;
}
.alerta.error {
  background-color: red;
}
.alerta.exito {
  background-color: green;
}
.alerta .closebtn {
  margin-left: 15px;
  color: white;
