
@include tablet {
    .seccion-icon {
        @include grid(3, 2rem);
    
    }
}


.icon {
    text-align: center;
    justify-content: center;
    img {
        height: 100px;
        width: 100px;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    h4 {
        display: block;
        font-weight: bold;
        font-size: 18px;
    }
    p{
        font-size: 16px;
    }
}
