.testimonios{
    width: 400px;
    padding-left: 2rem;
    margin-top: 50px;
}
.testimonio{
    background-color: #00cccc;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    color: #fff;
    margin: 0;
    padding: 20px;
    font-size: 18px;   
}

.nombre {
    background-color: #333;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    color: #fff;
    font-size: 18px;
    padding: 10px;
    text-align: center;
}

blockquote {
    padding: 0;
    margin: 0;
}

blockquote::after {
    border-color: transparent #00cccc transparent transparent;
    border-image: none;
    border-style: solid;
    border-width: 20px;
    top: 40px;
    content: " ";
    display: block;
    right: 75%;
    position: relative;
}