
.button-blue {
    background-color:#00cccc;
    color:white;
    font-weight: bold;
    text-decoration: none;
    text-align: center;
    margin-bottom: 1rem;
    border-radius: 5px;
    padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    display: block;
}

.button-blue:hover{
    background-color: darken($color: #00cccc, $amount: 7);
    cursor: pointer;
}



.button-gray {
    background-color: #333;
    color:white;
    font-weight: bold;
    text-decoration: none;
    text-align: center;
    margin-bottom: 1rem;
    padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    border: none;

}

.button-gray:hover{
    background-color: darken($color: #4C4E52, $amount: 8);
    cursor: pointer;

}

.button-table-red{
    display: block;
    background-color: red;
    border-radius: 5px;
    text-align: center;
    text-decoration: none;
    margin: 1rem;
    color: white;
    width:92%;
    padding: 4px;
    cursor: pointer;

}

.button-table-red:hover{
    background-color: darkorange;
}

.button-table-orange{
    display: block;
    background-color: orange;
    border-radius: 5px;
    text-align: center;
    text-decoration: none;
    margin: 1rem;
    color: white;
    border: 2px solid lightgray ;
    cursor: pointer;




}

.button-table-red:hover{
    background-color: darkred;
}


.button-login-register {
    background-color: #00cccc;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
  }
  
  .button-login-register:hover {
    opacity: 0.8;
  }
  
  .cancelbtn {
    width: auto;
    padding: 10px 18px;
    background-color: #f44336;
    color: white;
    cursor: pointer;
  }

  .cancelbtn:hover {
    opacity: 0.8;
  }
  
  .registerbtn {
    background-color: #00cccc;
    color: white;
    padding: 16px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    opacity: 0.9;
  }
  
  .registerbtn:hover {
    opacity:1;
  }

  .button-form{
    background-color: #00cccc;
    margin-top: 10px;
    margin-bottom: 10px;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

