
.inicio{
    background-image: url(../img/background-header.jpg);
}


.header {
    padding: 1rem 0 3rem 0;
    background-color: #333;

    img {
        height: 10rem;
        width: 10rem;
    }

    &.inicio {

        background-position: center center;
        background-size: cover;

        @include tablet {
            min-height: 30rem;
        }

        .contenido-header {
            @include tablet {
                min-height: 30rem;
            }
        }

    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    

    h1{
        text-align: left;
        font-size: 30px;
        font-weight: bold;
        line-height: 1;
    }

}